body{
  background: #ddd;
}
#root{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.todoInput{
   margin-bottom: 0.5em; 
}
.add-btn{
    margin-left: 0.5em;
    border-radius: 0.1em;
    background: #ccc;
    cursor: pointer;
}
#todoItem{
  margin-top: .3em;
  padding: 0.2em;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

#todoItem>#todo{
    -webkit-flex-grow: 1;
            flex-grow: 1;
}
#todoItem input[type=checkbox]{
   margin-right: 0.2em; 
}
#todoItem button{
   cursor: pointer;
}

.App {
  color: #333;
  border: 1px solid #aaa;
  background: white;
  margin: 2em;
  padding: 0 1em;
  min-height: 20em;
  min-width: 25em;
  box-shadow: 0 0 1em hsla(0,0%,0%,.15);
}
.App > .todoList > li:nth-child(odd){

  background: #eee;
}
.App > .todoList > li:nth-child(even){
  background: #aaa;
}
